<template>
    <d2-container>
        <!-- Banner管理 -->
        <!-- header -->
        <template slot="header">
            <el-row>
                <el-col :span="3" style="margin-right:10px;">
                    <!-- <el-autocomplete class="inline-input" v-model="state1" :fetch-suggestions="querySearch" placeholder="请输入内容"></el-autocomplete> -->
                    <el-alert title="Banner管理" type="info" :closable="false">
                    </el-alert>
                </el-col>
                <el-col :span="5">
                    <!-- <el-button type="primary" plain>查询</el-button> -->
                    <el-button type="primary" plain @click="add_good">添加</el-button>
                </el-col>
            </el-row>
        </template>
        <el-table :data="tableData" border style="width: 100%">

            <el-table-column type="index" width="50">
            </el-table-column>
            <el-table-column label="Banner图片" width="180">
                <template slot-scope="a">
                    <img :src="host+a.row.banner_image_address" alt="" style="width: 180px;">
                </template>
            </el-table-column>
            <el-table-column prop="banner_href_address" label="图片跳转地址" width="180">
            </el-table-column>
            <el-table-column prop="created_at" label="开始时间">
            </el-table-column>
            <el-table-column prop="updated_at" label="更新时间">
            </el-table-column>
            <el-table-column label="操作" width="280">
                <template scope="val">
                    <el-button type="primary" plain @click="edit_type(val.row)">编辑</el-button>
                    <!-- <el-button type="primary" plain>修改</el-button> -->
                    <el-button type="danger" plain @click="del_val(val.row)">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <!-- footer -->
        <el-row type="flex" justify="space-between" align="middle" style="height: 80px;">
            <el-col :span="24">
                <el-pagination layout="prev, pager, next" @current-change="page" :page-size="10" :total="checked" style="float: right;">
                </el-pagination>
            </el-col>
        </el-row>


        <!-- 添加banner -->
        <el-dialog :title="num" :visible.sync="centerDialogVisible" width="30%" center :before-close="clear">
            <el-form ref="form" :model="form" label-width="130px">
                <el-form-item label="选择banner图">
                    <el-upload action="" list-type="picture-card" :file-list="fileList" :limit="1" :on-exceed="file_false" :auto-upload="false"
                        :on-change="handlePictureCardPreview" :on-remove="handleRemove">
                        <i class="el-icon-plus"></i>
                    </el-upload>
                </el-form-item>
                <el-form-item label="banner跳转地址">
                    <el-input v-model="form.banner_href_address" placeholder="请输入banner跳转地址"></el-input>
                </el-form-item>
                <!-- <el-form-item label="起购价">
                    <el-input placeholder="请输入起购价" v-model="form.goods_type_leaset">
                    </el-input>
                </el-form-item> -->
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="clear">取 消</el-button>
                <el-button type="primary" @click="onSubmit" v-if="num!=='编辑banner'">确 定</el-button>
                <el-button type="primary" @click="onSubmit_type" v-if="num=='编辑banner'">确 定</el-button>
            </span>
        </el-dialog>

    </d2-container>

</template>

<script>
    import { show_banner, edit_banner, del_banner, add_banner, upload } from '../../plugin/axios/api.js';
    import { Myerror } from '../../plugin/axios/er.js';
    import qs from 'qs';
    export default {
        name: 'Banner_manage_home',
        data() {
            return {
                num: '',
                value3: '',
                state1: '',
                checked: 0,
                tableData: [],
                dialogVisible: false,
                pages: 1,
                centerDialogVisible: false,
                form: {
                    banner_image_address: '',
                    banner_href_address: '',
                },
                fileList: []
            }
        },
        methods: {
            querySearch(queryString, cb) {
                var restaurants = this.restaurants;
                var results = queryString ? restaurants.filter(this.createFilter(queryString)) : restaurants;
                // 调用 callback 返回建议列表的数据
                cb(results);
            },
            createFilter(queryString) {
                return (restaurant) => {
                    return (restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
                };
            },
            add_good() {//添加banner
                this.num = "添加banner";
                this.centerDialogVisible = true;
            },
            onSubmit() {//添加banner
                let par = qs.stringify(this.form)
                add_banner(par).then(res => {
                    this.msg(res)
                })
            },
            handleRemove(file, fileList) {
                console.log(file, fileList);
                this.fileList = [];
                this.$set(this.form, "banner_image_address", '')
            },
            handlePictureCardPreview(file) {
                // this.fileList.push(file)
                if (file.raw.size > 1024 * 1024 * 2) {
                    this.$message({
                        message: "请选择小于2M的图片！",
                        center: true,
                        type: 'error'
                    });
                    return
                }
                let param = new FormData()
                param.append('image', file.raw)
                param.append('path', "banner")
                upload(param).then(res => {
                    console.log(res.data);
                    this.$set(this.form, "banner_image_address", res.data.imagePath)
                })
            },
            file_false() {
                this.$message({
                    message: "已超出最大上传数量！",
                    center: true,
                    type: 'error'
                });
                return
            },
            edit_type(a) {//编辑banner
                this.num = "编辑banner";
                this.form = {
                    banner_image_address: a.banner_image_address,
                    banner_href_address: a.banner_href_address,
                    id: a.id
                }
                this.fileList = [{ name: "aaa", url: this.host + a.banner_image_address }]
                this.centerDialogVisible = true;
            },
            onSubmit_type() {//编辑banner
                let par = {
                    banner_image_address: this.form.banner_image_address,
                    banner_href_address: this.form.banner_href_address,
                }
                let id = this.form.id;
                edit_banner(par, id).then(res => {
                    this.msg(res)
                })
            },
            del_val(a) {//删除banner
                console.log(a);
                del_banner(a.id).then(res => {
                    this.msg(res)
                })
            },
            page(a) {
                console.log(a);
                this.pages = a;
                this.init_goods()
            },
            init_goods() {
                let par = {
                    page: this.pages,
                    limit: 10
                }
                show_banner(par).then(res => {
                    this.tableData = res.data.data;
                    this.checked = res.data.paginate.total;
                })
            },
            msg(res) {//返回参数处理
                if (res.code) {
                    this.$message({
                        message: res.message,
                        center: true,
                        type: 'success'
                    });
                    this.clear()
                    this.init_goods()

                    return
                }
                Myerror(res, this)
            },
            clear() {
                this.centerDialogVisible = false;
                this.form = {
                    banner_image_address: '',
                    banner_href_address: '',
                }
                this.fileList = [];

            }
        },
        mounted() {
            this.init_goods()
        },
        computed: {
            host() {
                return this.$store.state.d2admin.host
            }
        }
    }
</script>
<style lang="scss">
</style>